import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Router } from "@angular/router";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Message } from 'primeng/components/common/api';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { MyCustomMessBean } from 'app/pages/tzb/custom/custom-information/mine-custom/mine-custom-overview2/my-custom-message/bean/myCustomMess.bean';
import { tableMessage } from "app/@uisftech/common/constant/config";
@Component({
  selector: 'my-custom-message',
  templateUrl: './my-custom-message.component.html',
  styleUrls: ['./my-custom-message.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class MyCustomMessageComponent implements OnInit {

  param: any;
  isHoldCrdCard: any = [];
  myCustomMessBean: MyCustomMessBean = new MyCustomMessBean();
  msgs: Message[] = [];
  tablelist: any = [];
  totalNum: number = 0;
  type: string;
  apply: boolean = false;
  allot: boolean = false;
  transfer: boolean = false;
  first: number = 0;//分页控制
  scheduleId: string;
  taskCategoryId: string;
  templateId: any;
  messageTempleTotalType: any;
  createdTime: any;
  batchId: any;
  tabMesg: any = tableMessage;//表格中无数据时显示的内容

  constructor(
    public route: ActivatedRoute,
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
  ) {
  }

  ngOnInit() {
    if (this.route.snapshot.params['custTxtReportId']) {
      this.myCustomMessBean.custTxtReportId = this.route.snapshot.params['custTxtReportId'];
    }
    if (this.route.snapshot.params['batchId']) {
      this.myCustomMessBean.batchId = this.route.snapshot.params['batchId'];
    }
    
    this.myCustomMessBean.pageSize = 10;
    this.myCustomMessBean.pageNum = 1;
    this.query();
  }

  //初始查询
  query() {
    this.httpService.queryCustTxtApproval(this.myCustomMessBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.templateId = data.templateId;
        this.messageTempleTotalType = data.messageTempleTotalType;
        this.createdTime = data.createdTime;
        this.tablelist = data.resultPageList;
        this.totalNum = data.totalNum;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  
  //分页
  paginate(event) {
    this.myCustomMessBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.myCustomMessBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.myCustomMessBean.pageSize;
    this.query();
  } 
}
